.applyContainer{
    height: 79px;
    background-color: #ffffff;
    border-bottom: 1px solid #ececec;
    padding: 15px 10px 15px 18px;
    cursor: pointer;
}

.applyContainer img{
    width: 40px;
    height: 40px;
    -webkit-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
            box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
    border-radius: 40px;
    vertical-align: bottom;
}

.applyContainer .infosContainer{

}

.applyContainer .bold{
    font-family: Roboto;
    font-size: 14px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.71;
    letter-spacing: normal;
    color: rgba(0, 0, 0, 0.87);
}

.applyContainer .applyDate{
    float: right;
    font-family: Roboto;
    font-size: 12px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.67;
    letter-spacing: normal;
    text-align: right;
    color: rgba(0, 0, 0, 0.38);
}

.applyContainer .applyPrice{
    float: right;
    font-family: Roboto;
    font-size: 12px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 2;
    letter-spacing: normal;
    color: rgba(0, 0, 0, 0.87);
}

.btnAccept{
    background:#128e12;
    padding: 3px 10px;
    margin-bottom: 5px;
    line-height: 18px;
    width: 70px;
    border-radius: 2px;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.24), 0 0 2px 0 rgba(0, 0, 0, 0.12);
    box-shadow: 0 2px 2px 0
    rgba(0, 0, 0, 0.24), 0 0 2px 0 rgba(0, 0, 0, 0.12);
}

.btnReject{
    background:#b00907;
    padding: 3px 10px;
    margin-bottom: 5px;
    line-height: 18px;
    width: 70px;
    border-radius: 2px;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.24), 0 0 2px 0 rgba(0, 0, 0, 0.12);
    box-shadow: 0 2px 2px 0
    rgba(0, 0, 0, 0.24), 0 0 2px 0 rgba(0, 0, 0, 0.12);
}

.paymentAccept{
    font: 12px !important;
    color: #128e12;
    margin-top: 5px;
    margin-bottom: 5px;
}

.paymentReject{
    font: 12px !important;
    color: #b00a0a;
}








.modal {
    font-size: 12px;
  }
  .modal > .header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 5px;
  }
  .modal > .content {
    width: 100%;
    padding: 10px 5px;
  }
  .modal > .actions {
    width: 100%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
  }
  .modal > .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
  }


